.c-header {
  width: 100%;
  height: 68px;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;

  &__wrapper {
    margin-left: 5%;
    margin-right: 5%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;

    span {
      cursor: pointer;
      svg {
        width: 200px;
        height: 50px;
      }
    }

    .c-header__wrapper--list {
      margin-left: 2%;
      height: auto;
      font-weight: 400;
      cursor: pointer;

      a {
        color: black;
      }
    }

    svg {
      margin-left: auto;
      margin-right: 24px;
    }

    .c-header__wrapper--auth {
      border-radius: 3px;
      border: 0.8px solid gray;
      font-size: 12px;
      font-weight: 550;
      color: gray;
      padding: 3px 6px;
      margin-right: 8px;
        color: gray;
        cursor: pointer;
      }

  }
}

.svg-inline--fa {
  margin-left: 0px;
  padding-left: 15px;
  width: 20px;
  height: 20px;
}